.fv {
}
.fv::before {
  height: 100vh;
  width: 100vw;
  content: '';
  position: absolute;
  inset: 0;
  background: var(--gradeBG);
  opacity: 1;
  z-index: -1;
}

.gridContainer {
  position: relative;
  z-index: -1;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  width: 100vw;
  height: 100vh;
}
.gridItem {
  position: relative;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.6s ease;
}
.gridItem img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.4;
}
@media (max-width: 768px) {
  .gridContainer {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }
}
