.message {
  position: relative;
  overflow: hidden;
  background: none;
}

.gradientBg {
  position: absolute;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  filter: blur(100px);
  opacity: 0.3;
  z-index: -1;
}
.gradientTop,
.gradientBottom {
  position: absolute;
  z-index: -1;
}
.gradientTop {
  top: -200px;
  left: calc(50% - 400px);
  transform: translateX(-50%);
  background: radial-gradient(circle, rgba(255, 120, 0, 1), transparent);
}

.gradientBttom {
  bottom: -200px;
  left: calc(50% + 400px);
  transform: translateX(-50%);
  background: radial-gradient(circle, rgba(255, 120, 0, 1), transparent);
}

.num {
  overflow: hidden;
  display: inline-block;
}
.num p {
  display: inline-block;
  font-size: 2rem;
  font-weight: bold;
}
.messageBox {
  padding: 6rem 0 4rem;
}
.messageBox p {
  font-size: var(--fz20);
  line-height: 2.5;
  text-align: center;
  position: relative;
}
.messageBox p::before {
  content: '';
  z-index: -1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url(/images/section/MESSAGE.svg) no-repeat;
  background-size: 100% 100%;
  width: 80%;
  height: 100%;
}
